              <th>Foto</th>
              <th>Nome</th>
              <th>Descrição</th>
              <th>Quantidade</th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(material, index) in materialList" :key="index">
              <td>
                <img v-if="material.photo === ''" src="@/assets/default-image.png" height="80px" width="80px" alt="Foto do Material">
                <img v-else :src="material.photo" height="80px" width="80px" alt="Foto do Material">
